@import "sass/index";

$headerHeight: 81px;
$selectHeight: 71px;
$buttonsHeight: 89px;
$padding: spacing(4);
$rootPadding: spacing(8);

$pagination-background: white;
$pagination-darkness: rgb(234, 234, 234);

.root {
  color: white;
  width: 100%;
  overflow-x: hidden;
  position: relative; //
  max-height: MIN(
    calc(
      100vh - #{$headerHeight} - #{$selectHeight * 2} - #{$buttonsHeight} - #{$padding} -
        #{$rootPadding}
    ),
    540px
  );
}
.loader {
  width: 100%;
  height: 75%;

  display: flex;
  align-items: center;
  justify-content: center;
}
.dateSelector {
  display: flex;
  gap: 15px;

  @media (max-width: 370px) {
    flex-direction: column;
  }
}
.dateForm {
}
.dateUnit {
  & > div {
    background: map-get($grayColors, "100");
    border-radius: 5px;
  }
}
.tableWrapper {
  margin-top: 30px;
}
.paginationBlock {
  width: 100%;
  height: 40px;

  @media (max-width: 0.4px) {
    transform: scale(0.8);
  }
}
.paginationRoot {
  width: max-content;
  margin-left: auto;
}
.pagination {
  justify-content: center;
  & button {
    color: rgba($pagination-background, 0.5);
    background: rgba($pagination-background, 0);

    &:hover {
      background-color: rgba($pagination-darkness, 0.2);
    }
    &:focus {
      background-color: rgba($pagination-darkness, 0.2);
    }
    &:active {
      background-color: rgba($pagination-darkness, 0.2);
    }

    &:global.Mui-selected {
      color: #090c16;
      background: $pagination-background;
      &:hover {
        background-color: $pagination-darkness;
      }
      &:focus {
        background-color: $pagination-darkness;
      }
      &:active {
        background-color: $pagination-darkness;
      }
    }
  }
  & div {
    color: #424242;
  }
}
.paginationLoad {
  & .button {
    opacity: 0.75;
  }
}
