$boxHeight: 110px;
$background: #ffffff;

.root {
  & > div .text {
    text-align: left;
  }
}
.box {
  padding: 10px;
  background: $background;
  height: $boxHeight;
  border-radius: 10px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.text {
  white-space: nowrap;
  margin: 5px 0;
  color: black !important;
  font-family: "Gilroy";
  font-size: 16px;
  font-weight: bold;
}
.title {
  margin: 0;
  margin-left: 10px;
  font-size: 0.83rem;
  color: black !important;
}
.wrapper {
}
