@import "sass/index";

.root {
}
.info {
  & > :first-child {
    margin-bottom: 20px;
  }
}
.tableWrapper {
  overflow-x: auto;
}
.tableContainer {
  position: relative;
}
.table {
}

.tHead,
.tBody {
  & * {
    font-family: "Gilroy";
    font-weight: bold;
    text-align: center;
    line-height: 15px;

    @media (max-width: 600px) {
      font-size: 0.75rem;
    }
  }
}

.tHead {
  & * {
    font-size: 14px;
    color: $m_table-head;
    letter-spacing: 1px;
  }
}
.tCell {
  border-bottom-color: rgba($m_secondary-main, 0.2);
  padding: 15px;

  @media (max-width: 600px) {
    padding: 10px 15px;
  }

  & > p {
    white-space: nowrap;
  }
}

.tBody {
  & * {
    font-size: 16px;
    color: $m_table-body;
    letter-spacing: 0;
  }

  @media (max-width: $mobileScreen) {
    & * {
      font-weight: 600;
      color: $m_table-body-mobile;
      font-size: 0.75rem;
    }
  }
}

.lose {
  color: $lose-red !important;
}
.win {
  color: $win-green !important;
}
.coinWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon {
  width: 24px;
  height: 24px;
  cursor: pointer;
  & > path {
    fill: rgba(255, 255, 255, 0.65);
  }
}
.helpWrapper {
  position: relative;
  text-align: center;
}
.closeWrapper {
  position: fixed;
  top: 0;
  background: transparent;

  height: 100vh;
  width: 100vw;
  z-index: 1300;
}

.triangle {
  position: absolute;
  top: -20px;
  left: 15px;
  width: 0;
  height: 0;
  border-top: 10px solid #ffffff;
  border-right: 10px solid transparent;
  transform: rotate(-135deg);
  transition: opacity 0.4s;
  animation: show 0.4s cubic-bezier(0.4, 0, 0.2, 1);

  @media (max-width: 600px) {
    display: none;
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.coinIcon {
  position: static;
  margin: 0;
  margin-right: 5px;
}

.popper {
  z-index: 1300;
  margin-top: -100px;
}
